* {
    padding: 0;
    margin: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

a {
    text-decoration: none;
}
a:visited {
    text-decoration: none;
    color: white;
}
a:hover {
    color: yellow;
}

#mainHeading {
    text-align: center;
}

#wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
}

#editor {
    min-height: 400px;
    grid-column-start: 1;
    padding: 1.3em;
}

#preview {
    background-color: black;
    color: white;
    min-height: 400px;
    grid-column-start: 2;
    padding: 1.3em;
}

@media(max-width: 750px) {
    #wrapper {
        display: block;
    }

    #editor {
        width: 100%;
    }

    #preview{
        width: 100%;
    }
}